<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix header">
            <span>角色列表</span>
            <el-button type="primary" @click="$router.push('/roles/create')" round>创建</el-button>
        </div>
        <el-table
            :data="tableData"
            stripe
            style="width: 100%">
            <el-table-column label="编号" prop="role_id"></el-table-column>
            <el-table-column label="角色名称" prop="role_name"></el-table-column>
            <el-table-column label="角色描述" prop="role_describe"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="item">
                    <el-button type="primary" icon="el-icon-user" @click="changeHandler(item.row)" circle></el-button>
                    <el-button type="danger" icon="el-icon-delete" @click="delHandler(item.$index, item.row)" circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        
        <!-- 修改用户 -->
        <el-dialog
            title="角色分配"
            :visible.sync="isShow"
            width="30%"
            :before-close="handleClose">
                <el-form label-width="80px">
                    <el-tree
                    :props="defaultProps"
                    :data="data"
                    show-checkbox
                    node-key="auth_id"
                    :default-checked-keys="defaultCheck"
                    @check-change="handleCheckChange">
                    </el-tree>
                </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="isShow = false">取 消</el-button>
                <el-button type="primary" @click="onSubmit">确 定</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>
<script>
import {getRolesDataApi, delRoleApi, changeAuthsApi} from '@/api/roles.js'
import {getAuthsListApi} from '@/api/auths.js'
export default {
    data() {
        return {
            form: {
                role_id: '',
                auth_ids: [],
                auth_ids_son: []
            },
            isShow: false,
            tableData: [],
            data: [],
            defaultProps: {
                children: 'children',
                label: 'auth_name'
            },
            defaultCheck: [],
        }
    },
    methods: {
        delHandler(index, item) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // console.log(item);
                delRoleApi({
                    role_id: item.role_id
                })
                .then(res => console.log(res))
                this.tableData.splice(index, 1)
                this.$message.success('删除成功!');
            }).catch(() => {
                this.$message.info('已取消删除');          
            });
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        changeHandler(item) {
            this.isShow = true
            this.form.role_id = item.role_id

            getAuthsListApi().then(res => {
                // console.log(res)
                this.data = res.data
                // console.log(item.auth_ids);
                this.defaultCheck = item.auth_ids ? item.auth_ids.split(',') : []
            })
        },
        handleCheckChange(data, checked, indeterminate) {
            console.log(data, checked, indeterminate);
            if(data.children && checked == true) {
                this.form.auth_ids.push(data.auth_id)
                data.children.forEach(item => {
                    this.form.auth_ids.push(item.auth_id)
                    this.form.auth_ids_son.push(item.auth_id)
                })
            }else if(data.children && checked == false) {
                this.form.auth_ids.push(data.auth_id)
            }else {
                this.form.auth_ids.push(data.auth_id)
                this.form.auth_ids_son.push(data.auth_id)
            }
        },
        onSubmit() {
            let params1 = []
            let params2 = []
            this.form.auth_ids.forEach(item => {
                if(params1.indexOf(item) === -1) {
                    params1.push(item)
                }
            })
            this.form.auth_ids_son.forEach(item => {
                if(params2.indexOf(item) === -1) {
                    params2.push(item)
                }
            })

            params1 = params1.join(',')
            params2 = params2.join(',')
            changeAuthsApi({
                role_id: this.form.role_id,
                auth_ids: params1,
                auth_ids_son: params2
            })
            .then(res => {
                console.log(res);
            })
            this.isShow = false
        }
    
    },
    created() {
        getRolesDataApi().then(res => {
            // console.log(res);
            this.tableData = res.data
        })
    }
}
</script>
<style scoped>
    .el-pagination {
        width: 100%;
        padding: 10px 0;
        background: #ebeef5;
        text-align: right;
        margin-top: 10px;
        padding-right: 10px;
        box-sizing: border-box;
    }
    .el-card .header .el-button {
        float: right;
        margin-top: -10px
    }
</style>